
import { Vue, Options } from 'vue-class-component';
import { PlayerInfo,IList }  from "@/@types/type";
@Options({
  props: {
    list:Array,
    selected:Number,
    initText:String,
    disabled:Boolean
  },
  emits:['changeSelect']
})
export default class SelectList extends Vue {
    list!:IList[];
    selected!:number;
    initText:string;
    disabled:boolean;
    private changeSelect(value:string):void {
        const num:number = parseInt(value,10);
        this.$emit("changeSelect",num)
    }
}
