
import { Options, Vue } from 'vue-class-component';
import PointDisplay from "@/components/PointDisplay.vue";
import Player from "@/components/Player.vue";
import RoundInfo from "@/components/RoundInfoDisplay.vue";
import { PlayerInfo,TableInfo }  from "@/@types/type";
import TableStore from "@/store/TableStore";
@Options({
  components:{
    PointDisplay,
    Player,
    RoundInfo
  },
})
/**
 * 卓の描画
 */
export default class CenterTable extends Vue {
    private touchedPoint:number=0;
    private isTouch = false;
    get tableInfo():TableInfo {
      return TableStore.getTableInfo;
    }

    get playerInfo():PlayerInfo[] {
      return this.tableInfo.playerInfoList;
    }
    get diffPoint():number[] {
      let diffPoint:number[] =[0,0,0,0];
      diffPoint =  this.playerInfo.map((info:PlayerInfo)=>{
        return this.touchedPoint-info.point
      });
      return diffPoint;
    }
    private touchStart(value:number){
      this.touchedPoint = value;
      this.isTouch = true;
    }
    private touchEnd(){
      this.isTouch = false;
    }
}

