
import { Vue, Options } from 'vue-class-component';
import { PlayerInfo,IList,RoundInfo,MovePoint, TableInfo,Liquidation, NextHand }  from "@/@types/type";
import TableStore from "@/store/TableStore";
import _ from "lodash";


export default class Result extends Vue {
  get playerInfoList():PlayerInfo[] {
    return _.orderBy(TableStore.getPlayerInfo, [ 'point', 'id' ], [ 'desc', 'asc' ]);
  }
  private calc(rank:number,point:number){
    //1000で割ったポイントにする(五捨六入)
    const roundPoint = Math.round(((point-100)/1000));
    // 1位はオカで20P 
    console.log(rank);
    const okaPoint:number = rank === 1 ? 20:0;
    //4位→1位に10P 3位→2位に5P
    const result:number = roundPoint + this.umaCalc(rank) + okaPoint - 30;
    return result > 0 ? "+"+ result:result;
  }
  private umaCalc(rank:number){
    if(rank===1){
      return 10;
    }else if(rank===2) {
      return 5;
    }else if(rank===3) {
      return -5;
    }else {
      return -10;
    }
  }
  private finish():void {
    localStorage.removeItem('tableInfo');
    this.$router.push("/");
  }
}
