
import { Vue, Options } from 'vue-class-component';
import draggable from 'vuedraggable';

@Options({
  props: {
    title:String,
    group:String,
    list:Array
  },
  components:{
      draggable
  }
})
/**
 * ドラッグアンドドロップのコンポーネント
 */
export default class DraggableList extends Vue {
    title!:string
    group!:string
    list!:string[]

}
