
import { Options, Vue } from 'vue-class-component';
import PointDisplay from "@/components/PointDisplay.vue";
import Wind from "@/components/Wind.vue";
import {PlayerInfo} from "@/@types/type";
import TableStore from "@/store/TableStore";


@Options({           
  components:{
    PointDisplay,
    Wind
  },
  props:{
    playerInfo: {
      type:Object,
      default:{wind:"",reach:false,point:25000}
    },
    diffPoint:Number,
    isOtherTouch:Boolean
  }
})
/**
 * 卓のプレイヤー情報コンポーネント
 */
export default class PointDisp extends Vue {
  playerInfo!:PlayerInfo;
  diffPoint!:number;
  isOtherTouch!:boolean
  private isTouching:boolean = false;

  get dispPoint():number {
    return this.isOtherTouch ? this.diffPoint:this.playerInfo.point;
  }


  private doReach(id:number) {
    TableStore.toggleReach(id)
  }
  mounted(){
    const element:HTMLElement = (this.$refs.pointDisplay as PointDisplay).$el;
    const ua = navigator.userAgent.toLowerCase();
    const isSP = /iphone|ipod|ipad|android/.test(ua);
    const eventStart = isSP ? 'touchstart' : 'mousedown';
    const eventEnd   = isSP ? 'touchend' : 'mouseup';
    
    element.addEventListener(eventStart, (e:Event) => {
      e.preventDefault();
      this.isTouching = true;
      this.$emit("touchStart",this.playerInfo.point);
    });
    
    element.addEventListener(eventEnd, (e:Event)=> {
      e.preventDefault();
      this.isTouching = false;
      this.$emit("touchEnd");
    });
  }

}

