
import { Options, Vue } from 'vue-class-component';
import TableStore from "@/store/TableStore";
import { RoundInfo,MovePoint,Liquidation }  from "@/@types/type";
@Options({
  props: {
    finish: Number,
    winner: Number,
    feeder: Number,
  },
  emits:['update'],
})
/**
 * ロンで上がったときの清算処理
 */
export default class RonLiquidation extends Vue {
    winner!:number;
    feeder!:number;
    private movePoint:number = 0;

    get roundInfo():RoundInfo {
      return TableStore.getRoundInfo;
    }
    get sumPoint():number {
      let total:number = this.roundInfo.point+(this.roundInfo.keepParent * 300);
      if(this.movePoint) {
        total+=this.movePoint as number;
      }
      return total as number;
    }
    movePointFunc():Liquidation {
      const keepParent:boolean = this.winner === TableStore.getCurrentParent;
      //何点増える、減るかの情報を配列に格納 
      const movePointList:MovePoint[] = [
        {id:this.winner,point:this.sumPoint},
        {id:this.feeder,point:(this.movePoint*-1)}
      ];
      return {keepParentFlg:keepParent,movePointList:movePointList}
    }
    
}
