import Vuex from "vuex";
import { Module, VuexModule, Mutation, Action,getModule } from 'vuex-module-decorators'
import { PlayerInfo,TableInfo,InitRule,RoundInfo,NextHand } from "@/@types/type";
import _ from "lodash";

@Module({ dynamic: true, store: new Vuex.Store({}), name: "TableStore", namespaced: true })
class TableStore extends VuexModule{
  tableInfoHistory:TableInfo[] = [];
  tableInfo:TableInfo = {
    playerInfoList:[] as PlayerInfo[],
    roundInfo :{
      point:0,
      hand:1,
      keepParent:0,
      currentRound:"",
      isFinish:false
    } as RoundInfo,
    rule: {
      number:4,
      round:0
    }
  } as TableInfo;
  
  get getTableInfo(){
    return this.tableInfo;
  } 
  get getCurrentParent() {
    let parentId:number = 0;
    this.getPlayerInfo.forEach((info:PlayerInfo) => {
      if(info.wind === "東") {
        parentId = info.id;
      }
    })
    return parentId
  }
  get getPlayerInfo(){
    return this.tableInfo.playerInfoList;
  }
  get rule() {
    return this.tableInfo.rule
  }
  get finishCondPoint(){
    // 終了に必要な点数 3麻なら40000返し、4麻なら30000返し
    return this.rule.number === 3 ? 40000:30000;
  }
  get getRoundInfo() {
    return this.tableInfo.roundInfo;
  }
  get getCurrentRound() {
    return this.tableInfo.roundInfo.currentRound;
  }
  get getCurrentHand() {
    return this.tableInfo.roundInfo.hand;
  }

  @Mutation
  private saveTableInfo (tableInfo:TableInfo) {
    // 履歴に追加、10個超えたら古い履歴を削除
    if(this.tableInfoHistory && !_.isEqual(tableInfo,this.tableInfoHistory[0])){
      this.tableInfoHistory.unshift(_.cloneDeep(tableInfo));
      if(this.tableInfoHistory.length > 10) {
        this.tableInfoHistory.pop();
      }
    }
    console.log(this.tableInfoHistory);
    this.tableInfo = tableInfo;
  }
  @Action({rawError:true })
  public updateTableInfo (tableInfo:TableInfo) {
    this.saveTableInfo(tableInfo);
  }
  @Action({rawError:true })
  public resetReach(tableInfo:TableInfo):TableInfo {
    tableInfo.playerInfoList.forEach((playerInfo:PlayerInfo)=>{
      playerInfo.reach = false;
    });
    return tableInfo
  }
  @Action({rawError:true })
  public toggleReach (id:number) {
    const tableInfo:TableInfo = _.cloneDeep(this.getTableInfo);
    if(tableInfo.playerInfoList.length <= 0){
      alert("不具合っぽいので再読み込みして");
      return;
    }
    const player:PlayerInfo = tableInfo.playerInfoList[id]; 
    player.reach = !player.reach;
    if(player.reach) {
      // リーチした時
      player.point -= 1000;
      tableInfo.roundInfo.point +=1000;
    } else {
      // リーチを手動で解除したとき
      player.point += 1000;
      tableInfo.roundInfo.point -=1000;
    } 
    this.saveTableInfo(tableInfo);
  }
  @Action({rawError:true })
  public nextHand (nextHandInfo:NextHand) {
    const tableInfo:TableInfo = _.cloneDeep(nextHandInfo.tableInfo);
    const roundList:string[] = ["東","南","西","北"];
    this.resetReach(tableInfo);
    if(nextHandInfo.isKeepParent){
      //連荘
      tableInfo.roundInfo.keepParent++
    }else {
      //次の局へ
      tableInfo.roundInfo.hand++;
      tableInfo.roundInfo.keepParent=0;
      // 風をローテーションする
      tableInfo.playerInfoList.forEach((player:PlayerInfo)=> {
        const windIndex:number =roundList.findIndex((u)=>u === player.wind);
        player.wind = roundList[(windIndex+3)%4]
      })
    }
    //次の風になるか、ゲーム終了するか判定
    if(tableInfo.playerInfoList.some((player:PlayerInfo)=>{return player.point < 0})){
      // 誰か飛んでる場合 終局
      return true;
    }else if(tableInfo.roundInfo.hand > 4 || 
      (this.rule.round === 0 && this.getCurrentRound !== "東") ||
      (this.rule.round === 1 && this.getCurrentRound !== "東" && this.getCurrentRound !== "南")
    ){
      //局が4を超える または 東風戦で南場以降の場合 または 半荘戦で西場以降の場合
      const roundIndex:number = roundList.findIndex((u)=>u === this.getCurrentRound);
      let nextRoundFunc:(roundInfo:RoundInfo)=>boolean = (roundInfo:RoundInfo):boolean =>{
        if(tableInfo.playerInfoList.some((row:PlayerInfo)=>row.point >= 30000)){
          // 30000以上が存在したら終局
          return true;
        }else{
          // 30000以上が存在ない場合、続行 西(北)入
          if(tableInfo.roundInfo.hand > 4){
            tableInfo.roundInfo.hand = 1;
            tableInfo.roundInfo.currentRound = roundList[roundIndex+1];
          }
          return false;
        }
      }
      
      tableInfo.roundInfo.isFinish = roundIndex === -1 && !nextHandInfo.isKeepParent;
      if(this.rule.round === 1){
        // 半荘ルールの場合
        if(this.getCurrentRound !== "東"){
          tableInfo.roundInfo.isFinish = nextRoundFunc(tableInfo.roundInfo)
        }else{
            tableInfo.roundInfo.hand = 1;
            tableInfo.roundInfo.currentRound = roundList[roundIndex+1];
        }
      }else{
        // 東風ルールの場合
        tableInfo.roundInfo.isFinish = nextRoundFunc(tableInfo.roundInfo)
      }
    }  
    if(tableInfo.roundInfo.isFinish && tableInfo.roundInfo.point > 0){
      // 終わったときに供託が残っていたらトップが総取り
      const orderPointPlayerList:PlayerInfo[] = _.orderBy(this.getPlayerInfo, [ 'point', 'id' ], [ 'desc', 'asc' ]);
      tableInfo.playerInfoList[orderPointPlayerList[0].id].point+=tableInfo.roundInfo.point;
      tableInfo.roundInfo.point = 0;
    }
    this.saveTableInfo(tableInfo);
  }
  @Action({rawError:true })
  public backTableInfo () {
    if(this.tableInfoHistory.length <= 1){
      return;
    }
    this.tableInfoHistory.shift();
    this.saveTableInfo(_.cloneDeep(this.tableInfoHistory[0]));
  }
}
export default getModule(TableStore) as TableStore
