
import { Options, Vue } from 'vue-class-component';
import ToggleButton from "@/components/ToggleButton.vue";
import TableStore from "@/store/TableStore";
import { PlayerInfo,IList,Tempai,RoundInfo,MovePoint,Liquidation }  from "@/@types/type";
@Options({
  props: {
    finish: Number
  },
  components:{
    ToggleButton,
  }  
})
/**
 * 流局時の清算処理
 */
export default class DrawLiquidation extends Vue {

    private movePoint:number = 0;
    private tempaiList:Tempai[] = this.joinList.map((row:IList)=>{
      return Object.assign(row,{checked:false});
    });
    get parentId():number {
      return TableStore.getCurrentParent;
    }
    get roundInfo():RoundInfo {
      return TableStore.getRoundInfo;
    }
    get joinList():IList[] {
      return TableStore.getPlayerInfo.map((playerInfo:PlayerInfo)=>{
        return {
          id:playerInfo.id,
          name:playerInfo.name
        };
      })
    }
    
    get fine():{winnerPay:number,feederPay:number}{
      let tempaiNum:number = this.tempaiList.filter(row => row.checked).length;
      if(tempaiNum === 0 || tempaiNum === 4) {
        return {winnerPay:0,feederPay:0}
      }else if(tempaiNum === 1) {
        return {winnerPay:3000,feederPay:-1000}
      }else if(tempaiNum === 2) {
        return {winnerPay:1500,feederPay:-1500}
      }else {
        return {winnerPay:1000,feederPay:-3000}
      }
    } 
    private updateTempai(item:Tempai) {
      this.tempaiList = this.tempaiList.map((row:Tempai)=>{
        row.checked = item.id === row.id ? item.checked:row.checked;
        return row;
      });
    }
    movePointFunc():Liquidation {
      let keepParent:boolean = false;
      // 各プレイヤーが何点増える、減るかの情報を配列に格納 
      const movePointList:MovePoint[] =  this.tempaiList.map((row:Tempai)=>{
        if(row.id === this.parentId){
          keepParent = row.checked;
        }
        const movePoint:number = row.checked ? this.fine.winnerPay:this.fine.feederPay;
        return {id:row.id,point:movePoint};
      });
      return {keepParentFlg:keepParent,movePointList:movePointList};
    }
}
