export class PlayerInfo {
    static i:number = 0
    id:number
    name:string
    wind:string
    point:number
    reach:boolean
    public constructor(point:number,wind:string, name?:string){
        this.id = PlayerInfo.i;
        this.name = name || "player"+(this.id + 1);
        this.wind = wind;
        this.point = point;
        this.reach = false;
        PlayerInfo.i++;
    }
}
export interface TableInfo {
    playerInfoList:PlayerInfo[];
    roundInfo:RoundInfo;
    rule:InitRule;
}
export interface RoundInfo {
    point:number;
    hand:number;
    keepParent:number;
    currentRound:string; //東場 or 南場 or ...
    isFinish:boolean;

}
export interface InitRule {
    number:number;
    round:number; //0:東風、1:半荘
}
export interface IList {
    id:number;
    name:string;
}
export interface Tempai extends IList {
    checked:boolean;
}
export interface MovePoint {
    id:number;
    point:number;
}
export interface Liquidation {
    keepParentFlg:boolean;
    movePointList:MovePoint[];
}
export interface NextHand {
    tableInfo:TableInfo;
    isKeepParent:boolean;
}