
import { Options, Vue } from 'vue-class-component';
import draggable from 'vuedraggable';
import TableStore from "@/store/TableStore";
import DraggableList from "@/components/DraggableList.vue"
import { PlayerInfo,TableInfo }  from "@/@types/type";

@Options({
  components: {
      draggable,
      DraggableList
  },
})
export default class Initialize extends Vue {
    private number:number = 4;
    private round:number = 1;
    private playerName:string = "";
    private nameList:string[] = [];
    private particepateList:string[] = [];
    private removeList:string[] = [];
    get isStartable():boolean {
        return this.particepateList.length !== this.number;
    }
    get windList():string[] {
        const windList:string[] = ["東","南","西","北"];
        return windList;
    }
    created() {
        this.nameList = localStorage.name ? JSON.parse(localStorage.name):[];
        const tableInfo:TableInfo = localStorage.tableInfo ? JSON.parse(localStorage.tableInfo):{} as TableInfo;
        if(tableInfo && tableInfo.playerInfoList && tableInfo.playerInfoList.length > 0){
            if (window.confirm("前回の続きから始めますか？")) {
                TableStore.updateTableInfo(tableInfo);
                this.$router.push("/table");
            }else{
                localStorage.removeItem('tableInfo');
            }
        }
    }

    private add(name:string):void{
        if(name === ""){
            alert("空白、ダメ");
            return;
        }
        this.nameList.push(name);
        localStorage.name = JSON.stringify(this.nameList);
    }
    private remove():void{
        this.removeList.forEach((str:string,index:number)=>{
            const idx:number = this.nameList.indexOf(str)
            if(idx >= 0 ){
             this.nameList.splice(idx, 1); 
            }
            this.removeList.splice(index,1);
        });
        localStorage.name = JSON.stringify(this.nameList);
    }
    private gameStart():void{
        const initPoint:number = this.number === 4 ? 25000 :35000;
        const playerInfoList:PlayerInfo[] = this.windList.map((wind:string,index:number)=>{
            let  playerInfo:PlayerInfo = {} as PlayerInfo
            if(this.particepateList.length > index) {
               playerInfo = new PlayerInfo(initPoint,wind,this.particepateList[index])
            } else {
               playerInfo = new PlayerInfo(0,wind,"");
            }
            return playerInfo;
        });
        const initTableInfo:TableInfo = {
            playerInfoList:playerInfoList,
            roundInfo: {
                point:0,
                hand:1,
                keepParent:0,
                currentRound:"東",
                isFinish:false
            },
            rule:{number:this.number,round:this.round}
        }
        TableStore.updateTableInfo(initTableInfo);

        this.$router.push("/table");
    }
}
