
import { Vue, Options } from 'vue-class-component';
@Options({
  props: {
    width:String,
    height:String
  },
})
/**
 * ダイアログコンポーネント
 */
export default class Dialog extends Vue {
    width!:string;
    height!:string;
    get contentStyle():Object {
        return {
            "position":"absolute",
            "left":"calc(50% - ("+this.width+"/2))",
            "top":"calc(50% - ("+ this.height+"/2))",
            "width":this.width,
            "height":this.height,
        }
    }
}
