
import { Options, Vue } from 'vue-class-component';
import Dialog from "@/components/Dialog.vue";
import SelectList from "@/components/SelectList.vue";
import RonLiquidation from "@/components/Liquidation/RonLiquidation.vue"
import TumoLiquidation from "@/components/Liquidation/TumoLiquidation.vue"
import DrawLiquidation from "@/components/Liquidation/DrawLiquidation.vue"
import { PlayerInfo,IList,RoundInfo, TableInfo,Liquidation, NextHand }  from "@/@types/type";
import TableStore from "@/store/TableStore";
import _ from "lodash";

@Options({
  props: {
    wind: String,
    id:Number
  },
  components:{
    Dialog,
    SelectList,
    RonLiquidation,
    TumoLiquidation,
    DrawLiquidation
  }
})
export default class PointDisp extends Vue {
    wind!:string;
    id!:number;
    private dispFlg:boolean = false;
    // 上がり方
    private finish:number = 0;
    // 和了者
    private winner:number = -1;
    // 放銃者
    private feeder:number = -1;
    private finishList:IList[] = [
      {id:0,name:"ロン"},
      {id:1,name:"ツモ"},
      {id:2,name:"流局"}
    ]
    get pointResult():Liquidation {
      if(this.finish === 0) {
        return (this.$refs.RonLiquidation as RonLiquidation).movePointFunc();
      }else if(this.finish === 1){
        return (this.$refs.TumoLiquidation as TumoLiquidation).movePointFunc();
      } else {
        return (this.$refs.DrawLiquidation as DrawLiquidation).movePointFunc();
      }
    }
    get parentId():number {
      return TableStore.getCurrentParent;
    }
    get roundInfo():RoundInfo {
      return TableStore.getRoundInfo;
    }
    get joinList():IList[] {
      return TableStore.getPlayerInfo.map((playerInfo:PlayerInfo)=>{
        return {
          id:playerInfo.id,
          name:playerInfo.name
        };
      })
    }
    get liquidationDisabled():boolean {
      const ron:boolean = this.finish === 0;
      return (ron && (this.winner === -1 || this.feeder === -1))|| 
        (this.finish === 1 && this.winner === -1) ||
        (ron && this.winner === this.feeder);
    }
    private clickWind():void {
      if(this.id === 0){
        this.dispFlg = true;
      }
    }
    private changeFinish(value:number):void {
      this.finish = value;
    }
    private changeWinner(value:number):void {
      this.winner = value;
    }
    private changeFeeder(value:number):void {
      this.feeder = value
    }
    // 清算
    private liquidation():void {
      const tableInfo:TableInfo =  _.cloneDeep(TableStore.getTableInfo);
      // 点数移動
      tableInfo.playerInfoList.forEach((info:PlayerInfo)=>{
        const i:number = this.pointResult.movePointList.findIndex((u)=> u.id === info.id)
        if(i >= 0){
          info.point +=this.pointResult.movePointList[i].point
        } 
      })
      if(this.finish !== 2) {
        //流局以外の場合、供託リセット
        tableInfo.roundInfo.point = 0;
      }
      // 次の局へ
      TableStore.nextHand({
        tableInfo:tableInfo,
        isKeepParent:this.pointResult.keepParentFlg,
      } as NextHand);
      this.dispFlg =false;

      // 終局の場合
      if(this.roundInfo.isFinish){
        // リザルト画面に飛ぶ
        this.$router.push('/result');
      }
    }
}
