
import { Vue } from 'vue-class-component';
import TableStore from "@/store/TableStore";
import { TableInfo,RoundInfo }  from "@/@types/type";

/**
 * 持ち点表示コンポーネント
 */
export default class RoundInfoDisplay extends Vue {
    get tableInfo():RoundInfo {
      return TableStore.getRoundInfo;
    }
    get reachNum():number {
      return this.tableInfo.point/1000;
    }
}
